<template>
  <div id="app">
    <h1>Mars Rover</h1>
    
    <input v-model="coordinateX" type="text" placeholder="Digite a coordenada X" />
    <input v-model="coordinateY" type="text" placeholder="Digite a coordenada Y" />
    
    <button @click="saveMarsCoordinates">Salvar Coordenadas</button>

    <button @click="createInputs">Criar Rover</button>

    <div v-for="(inputs, index) in inputsList" :key="index">
      <input 
        type="text" 
        v-model="inputs.input1" 
        placeholder="Informa a posição initial do rover (X, Y, N)" 
      />
      <input 
        type="text" 
        v-model="inputs.input2" 
        placeholder="Instruções de movimentação" 
      />

      <button @click="sendRoverInstructions(index)">Enviar Dados</button>
    </div>

    <div v-if="responseData">
      <h3>Posição final do rover:</h3>
      <p>{{ responseData }}</p>
    </div>
    
    <Toast v-if="showToast" :message="toastMessage" :type="toastType" />
  </div>
</template>

<script>
  import axios from 'axios';
  import Toast from './components/Toast.vue';

  export default {
    name: 'App',
    components: {
      Toast
    },
    data() {
      return {
        apiUrl: process.env.VUE_APP_API_URL,
        coordinateX: '', 
        coordinateY: '', 
        responseMessage: '',
        showToast: false,
        toastMessage: '',
        toastType: 'success',
        inputsList: [],
        responseData: null
      };
    },
    mounted() {
      this.getMarsCoordinates();
    },
    methods: {
      async getMarsCoordinates() {
        try {
          const response = await axios.get(`${this.apiUrl}/getMarsCoordinates`);
          this.coordinateX = response.data.coordinateX || '';
          this.coordinateY = response.data.coordinateY || '';
        } catch (error) {
          console.error('Erro ao buscar coordenadas', error);
        }
      },
      async saveMarsCoordinates() {
        try {
          const response = await axios.post(`${this.apiUrl}/saveMarsCoordinates`, {
            coordinateX: this.coordinateX,
            coordinateY: this.coordinateY
          });

          this.showToastMessage(response.data.message, 'success');
        } catch (error) {
           this.showToastMessage(response.data.message, 'error');
        }
      },
      addInputs() {
        this.inputsList.push({
          input1: '',
          input2: '',
        });
      },
      async sendRoverInstructions(index) {
        const inputs = this.inputsList[index];

        try {
          const response = await axios.post(
            `${this.apiUrl}/saveRoveInstructions`,
            { input1: inputs.input1, input2: inputs.input2 }
          );

          this.responseData = response.data.message;
        } catch (error) {
          console.error("Erro ao enviar dados:", error);
        }
      },
      showToastMessage(message, type) {
        this.toastMessage = message;
        this.toastType = type;
        this.showToast = true;

        setTimeout(() => {
          this.showToast = false;
        }, 3000);
      }
    }
  };
</script>

<style>
#app {
  text-align: center;
  margin-top: 50px;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  padding: 20px;
}

input {
  padding: 10px;
  margin-right: 10px;
}

button {
  margin: 10px 0;
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #368f6a;
}

input {
  margin: 5px;
  padding: 8px;
  font-size: 16px;
}
</style>
